.speech-bubble {
	position: relative;
	background: #c4d7d9;
	border-radius: .4em;
    

}

.speech-bubble:after {
	content: '';
	position: absolute;
	top: 0;
	left: 50%;
	width: 0;
	height: 0;
	border: 23px solid transparent;
	border-bottom-color: #c4d7d9;
	border-top: 0;
	border-right: 0;
	margin-left: -11.5px;
	margin-top: -23px;
}